import React from 'react';
import { Popover, PopoverHeader, PopoverBody } from 'reactstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faWineBottle } from '@fortawesome/free-solid-svg-icons';

const options = [
            {name: '', value: 'beverages'},
            {name: ' ', value: 'nonalchool'},
            {name: '', value: 'vodka'},
            {name: '', value: 'koniak'},
            {name: '', value: 'gin'},
            {name: '', value: 'wine'},
            {name: '', value: 'liquer'},
            {name: '', value: 'beer'},
            {name: '', value: 'ouzo'},
            {name: '', value: 'whiskey'},
            {name: '', value: 'rum'},
            {name: '', value: 'tequila'},
            {name: '', value: 'tsipouro'},
            {name: '', value: 'snacks'}           
        ];
        
export default class ProductInfo extends React.Component {
    constructor(props) {
        super(props);
        this.toggle = this.toggle.bind(this);
        this.state = {
            popoverOpen: false
        };
        this.category = null;
        for (var i in options) {
            if (options[i].value === this.props.price.category) this.category = options[i].name; 
        }
    }
    
    toggle() {
        this.setState({
            popoverOpen: !this.state.popoverOpen
        });
    }

    render() {
        return (
            <div>
                <button className="search_btn" id={"product_btn" + this.props.price.id} title=' '>
                    <FontAwesomeIcon icon={faWineBottle}></FontAwesomeIcon>
                </button>
                <Popover placement="bottom" isOpen={this.state.popoverOpen} target={"product_btn" + this.props.price.id} toggle={this.toggle}>
                    <PopoverHeader> </PopoverHeader>
                    <PopoverBody>
                        <strong>Barcode: </strong>
                        {this.props.price.barcode}<br/>
                        <strong>: </strong>
                        {this.props.price.productName}<br/>
                        <strong>: </strong>
                        {this.props.price.brand}<br/>
                        <strong>: </strong>
                        {this.props.price.volume}ml<br/>
                        <strong>: </strong>
                        {this.category}<br/>
                        <strong>: </strong>
                        {this.props.price.productTags.join(', ')}<br/>
                        <strong>: </strong>
                        {this.props.price.description}<br/>
                        {this.props.price.productWithdrawn 
                        ? <p className='withdrawnShop'>  </p>
                        : <p className='activeShop'>  </p>
                        }
                    </PopoverBody>
                </Popover>
            </div>
        );
    }
}

